/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
body {
  margin: 0;
  overflow-x: hidden;
}

:root {
  --margin-y-1: 3rem;
  --margin-y-2: 6rem;
  --padding-x:20px;
  --padding-x-header:20px;
  --padding-x-decor:var(--padding-x);
  --padding-x-pleine-largeur:0;
  --largeur-max:1920px;
  /*Valeurs en dur pour les navigateurs qui ne supportent pas clamp*/
  --hauteur-banniere:400px;
}
@supports (height: clamp(560px, 78vw, 1083px)) {
  :root {
    --hauteur-banniere:clamp(220px,28vw,550px);
  }
}
@media (min-width: 960px) {
  :root {
    --padding-x: 10vw;
    --padding-x-header:5vw;
  }
}
@media (min-width: 1440px) {
  :root {
    --padding-x: calc(50vw - 1114px/2);
    --padding-x-header:var(--padding-x);
  }
}
@media (min-width: 1920px) {
  :root {
    --padding-x: calc((var(--largeur-max) - 1114px)/2);
    --padding-x-pleine-largeur: calc(50vw - var(--largeur-max)/2);
  }
}

.site-container {
  max-width: var(--largeur-max);
  margin-left: auto;
  margin-right: auto;
}

.site-main .fil-ariane, .site-main .entry-title {
  padding-left: var(--padding-x);
  padding-right: var(--padding-x);
}

body .entry-content {
  padding: 0 var(--padding-x) 1px;
}

.alignfull, .bleed-container {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 1920px) {
  .alignfull, .bleed-container {
    width: var(--largeur-max);
  }
}

.container {
  padding: 0 var(--padding-x);
}

/* Hauteur mini main*/
.site {
  overflow-x: hidden;
  position: relative;
}

.site-main {
  min-height: 50vh;
}

.show-for-md,
.show-for-lg {
  display: none;
}

@media (min-width: 768px) {
  .show-for-md {
    display: initial;
  }
  .hide-for-md {
    display: none;
  }
}
@media (min-width: 960px) {
  .show-for-lg {
    display: initial;
  }
  .hide-for-lg {
    display: none;
  }
}
