--------------------------------------------------------------*/
.site-footer {
  background-color: var(--wp--preset--color--primaire);
  background-image: url("icons/decor-xl.svg");
  background-position: center bottom;
  background-repeat: no-repeat;
  color: var(--wp--preset--color--blanc);
  padding: 2.5rem var(--padding-x) 1rem;
  position: relative;
  --largeur-col: 27rem;
}
.site-footer .col {
  margin-bottom: 2rem;
}
@media (min-width: 500px) {
  .site-footer .colonnes-footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
  .site-footer .col:nth-of-type(3) {
    grid-column: 1/span 2;
  }
}
@media (min-width: 768px) {
  .site-footer {
    padding-top: 5rem;
  }
  .site-footer .colonnes-footer {
    display: flex;
    justify-content: space-around;
  }
  .site-footer .nav-social {
    margin-top: 5rem;
  }
}
.site-footer a {
  color: var(--wp--preset--color--blanc);
  text-decoration: none;
}
.site-footer a:hover, .site-footer a:focus {
  text-decoration: underline;
}
.site-footer .menu li {
  margin-bottom: 0.4rem;
}
.site-footer .menu li a {
  font-weight: 700;
  text-transform: uppercase;
}
.site-footer .nav-social {
  width: 100%;
}
.site-footer .nav-social ul {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  width: 100%;
}
.site-footer .nav-social ul li a {
  --size: 45px;
  background-image: url("icons/facebook-blanc.svg");
  background-repeat: no-repeat;
  background-size: contain;
  color: transparent;
  display: block;
  flex: 0 0 var(--size);
  height: var(--size);
  margin-right: 30px;
  overflow: hidden;
  width: var(--size);
}
.site-footer .nav-social ul li a[href*=twitter] {
  background-image: url("icons/twitter.jpg");
}
.site-footer .nav-social ul li:last-of-type a {
  margin-right: 0;
}
.site-footer .copyright {
  text-align: center;
}
.site-footer .copyright span,
.site-footer .copyright a {
  color: var(--wp--preset--color--blanc);
  display: inline-block;
  font-weight: 500;
  margin-bottom: 1rem;
  font-size: calc(10 * var(--taille-texte) / 15);
}
.site-footer .copyright .sep {
  display: inline-block;
  margin: 0 4px;
}
.site-footer .backtotop {
  --taille:40px;
  align-items: center;
  background-color: var(--wp--preset--color--secondaire);
  border: none;
  border-radius: 50%;
  display: flex;
  height: var(--taille);
  padding: 8px;
  width: var(--taille);
  top: var(--taille);
  position: absolute;
  right: 10px;
  top: calc(-10px - var(--taille));
  transition: all var(--transition);
  z-index: 1;
}
.site-footer .backtotop svg {
  position: relative;
  top: -2px;
  transform-origin: center;
  transform: rotateZ(180deg);
}
.site-footer .backtotop svg path {
  fill: var(--wp--preset--color--blanc);
}
.site-footer .backtotop:hover, .site-footer .backtotop:focus {
  background-color: var(--wp--preset--color--tertiaire);
  border: none;
}
.site-footer .backtotop:hover svg path, .site-footer .backtotop:focus svg path {
  fill: var(--wp--preset--color--blanc);
}

.loop {
  list-style: none;
  margin: 0;
  padding-top: var(--margin-y-1);
}
.loop .post-summary {
  margin-bottom: var(--margin-y-1);
}
@media (min-width: 960px) {
  .loop {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--margin-y-1);
    margin-bottom: var(--margin-y-1);
  }
  .loop .post-summary {
    margin-bottom: 0;
    max-width: unset;
    width: 100%;
  }
}

.blog h1.entry-title {
  color: var(--wp--preset--color--primaire);
}
.blog h1.entry-title::before {
  background-image: url("icons/picto-fruit-rouge.png");
}

form.search-form {
  align-items: stretch;
  display: flex;
  max-width: 50rem;
}
form.search-form label {
  flex-grow: 1;
}
form.search-form input.search-field {
  height: 42px;
  width: 100%;
}
form.search-form input.search-submit {
  margin-bottom: 0;
  min-width: unset;
}

.search-results li.product {
  margin-bottom: var(--margin-y-2);
  text-align: left;
}

.no-results {
  min-height: 50vh;
}
.no-results .entry-title {
  padding-top: var(--margin-y-1);
}

.single-title {
  align-items: flex-start;
  display: flex;
  margin: 0;
}
.single-title::before {
  flex-basis: 3.6rem;
  flex-grow: 0;
  flex-shrink: 0;
  top: 0;
}

.single-date {
  color: var(--wp--preset--color--secondaire);
  margin-bottom: 3rem;
  padding-left: 4.2rem;
}

.single-post div.entry-content {
  padding-bottom: var(--margin-y-1);
  padding-top: var(--margin-y-1);
}
.single-post .thumbnail {
  margin-bottom: var(--margin-y-1);
}
.single-post .thumbnail img {
  margin: 0 auto;
}
.single-post .retour {
  font-size: calc(20 * var(--taille-texte) / 15);
  text-align: center;
}
@media (min-width: 768px) {
  .single-post .entry-content {
    display: grid;
    column-gap: 40px;
    grid-template-columns: 1fr 1fr;
  }
  .single-post .retour {
    grid-column: 1/span 2;
    margin-top: var(--margin-y-1);
  }
  .single-post.no-thumbnail .entry-content {
    display: block;
  }
}
@media (min-width: 960px) {
  .single-post .entry-content {
    grid-template-columns: 60% 40%;
  }
}

.wp-block-group.alignfull {
  --wp--style--block-gap:0;
}
.wp-block-group.alignfull .wp-block-image, .wp-block-group.alignfull .wp-block-columns > .wp-block-column {
  margin: 0;
}
.wp-block-group.alignfull .wp-block-column > .wp-block-image {
  height: 100%;
}
.wp-block-group.alignfull .wp-block-column > .wp-block-image img {
  height: 100%;
  width: 100%;
}
.wp-block-group.alignfull .wp-block-columns {
  margin-bottom: 0;
}
.wp-block-group.bloc-bleu {
  background-color: var(--bleu);
  padding: 3rem;
}
.wp-block-group.bloc-bleu p, .wp-block-group.bloc-bleu h3, .wp-block-group.bloc-bleu h4 {
  color: var(--wp--preset--color--blanc);
}
@media screen and (max-width: 781px) {
  .wp-block-group.bloc-bleu {
    width: 100vw;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-bottom: -1.8em;
  }
}
@media screen and (max-width: 781px) and (min-width: 1920px) {
  .wp-block-group.bloc-bleu {
    width: var(--largeur-max);
  }
}

.wp-block-columns.alignfull {
  margin-bottom: 0;
  padding: var(--margin-y-2) var(--padding-x);
}
.wp-block-columns a.wp-block-button__link {
  min-width: 10rem;
}

.wp-block-column.gap-fallback {
  padding-left: 2em;
}
@supports (font-size: clamp(1em, 10vw, 10em)) {
  .wp-block-column.gap-fallback {
    padding-left: 0;
  }
}

.wp-block-image.alignwide {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 var(--padding-x);
}
@media (min-width: 1920px) {
  .wp-block-image.alignwide {
    width: var(--largeur-max);
  }
}
.wp-block-image.alignwide figcaption {
  text-align: right;
}

.wp-block-code {
  background-color: lightgrey;
  margin-bottom: var(--margin-y-2);
  padding: 2rem;
  font-style: italic;
}

.filtre-archive {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  padding-top: 2rem;
}
.filtre-archive label {
  color: var(--wp--preset--color--primaire);
  cursor: pointer;
  font-size: calc(20 * var(--taille-texte) / 15);
  font-weight: 400;
  margin: 0 1.5rem 1rem 0;
  padding: 0;
  position: relative;
  transition: all 0.4s linear;
}
.filtre-archive label:hover, .filtre-archive label:focus {
  text-decoration: underline;
}
.filtre-archive input {
  display: block;
  appearance: auto;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}
.filtre-archive input:focus-visible + label {
  text-decoration: underline;
}
.filtre-archive input:checked + label {
  font-weight: 700;
}
@media (min-width: 960px) {
  .filtre-archive {
    justify-content: center;
    margin-bottom: 2rem;
  }
  .filtre-archive label {
    padding: 0.5rem 2.5rem;
  }
}

.pagination {
  align-items: center;
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0 var(--padding-x);
  margin: var(--margin-y-1) 0;
}
.pagination li::after {
  color: var(--wp--preset--color--primaire);
  content: "-";
  display: inline-block;
  font-size: calc(20 * var(--taille-texte) / 15);
  font-weight: 700;
}
.pagination li:last-of-type::after {
  display: none;
}
.pagination a.page {
  font-size: calc(20 * var(--taille-texte) / 15);
  color: var(--wp--preset--color--primaire);
  font-weight: 700;
  margin: 0 0.5rem;
  padding: 0.5rem;
  text-decoration: none;
}
.pagination a.page:hover, .pagination a.page:focus {
  text-decoration: underline;
}
.pagination li.active a {
  text-decoration: underline;
}
.pagination li.disabled a {
  cursor: default;
}
.pagination li.disabled a:hover, .pagination li.disabled a:focus {
  border-color: transparent;
  color: var(--bleu);
}

.post-summary {
  display: flex;
  flex-direction: column;
  margin: 0 auto var(--margin-y-1);
  max-width: 46rem;
  position: relative;
}
.post-summary p.entry-date {
  color: var(--wp--preset--color--primaire);
}
.post-summary a.suite {
  align-items: center;
  border-radius: 50%;
  display: flex;
  height: 2.6rem;
  justify-content: center;
  margin-bottom: 0;
  padding: 0;
  padding-left: 2px;
  width: 2.6rem;
}
.post-summary a.suite svg {
  max-height: 60%;
  max-width: 90%;
}

.post-summary__title {
  line-height: 1.2;
  margin-bottom: 0;
}
.post-summary__title a {
  text-decoration: none;
}

.post-summary__image {
  display: block;
  margin-bottom: 0.5rem;
}
.post-summary__image img {
  aspect-ratio: 1.36;
  display: block;
  width: 100%;
}
.post-summary__image.contain {
  border-bottom: solid 1px lightgrey;
  padding: 1rem;
}
.post-summary__image.contain img {
  object-fit: contain;
}
@supports not (aspect-ratio: auto) {
  .post-summary__image {
    height: 257px;
  }
  .post-summary__image img {
    height: 100%;
  }
}

@media (min-width: 768px) {
  .post-summary {
    flex-direction: column;
  }
  .post-summary__image {
    flex: unset;
    margin-right: unset;
  }
  .post-summary__content {
    flex-grow: 1;
  }
}